/**
 * 生產設置
 */
import React, {PureComponent} from "react";
import moment from 'moment';
import {connect} from "react-redux";
import {Button, Col, DatePicker, Form, Input, message, Modal, Row, Select, Table} from 'antd';
import {Layout} from "./style";
import *  as api from "../../../axios";
import CodeModal from './modalQrcode'
import {BASE_URL} from "../../../axios/config";
import AuthComponent from "../../common/authComponent ";
import LineWrap from '../../common/lineWrap';
import {toJS,set} from 'immutable';
import {actionCreators} from "../../common/store";
import "./resize.css"
import ResizeableTitle from "../../common/resizable";
const AuthButton = AuthComponent(Button);
const {confirm} = Modal;
const {Option} = Select;
const FormItem = Form.Item;



//新增 编辑弹窗
const ModalForm = Form.create()(props => {
    const {form, title, visible, handleCancel, current, bridges, bodyTypes, setValue} = props;
    let scheduleInfos = props.scheduleInfos.toJS?props.scheduleInfos.toJS():props.scheduleInfos;
    const {getFieldDecorator, getFieldValue} = form;
    const isEdit = Object.keys(current).length != 0;
    const currentBridge = bridges&&bridges.find(bridge => bridge.v == current.bridgeName)
    const currentBodyType = bodyTypes&&bodyTypes.find(bodyType => bodyType.v == current.typeName)

    const handleChange = (value,option,type)=>{
        if (type =='name') {
            setValue({bridgeId:value,bodyType:scheduleInfos.bodyType})
        } else {
            setValue({bridgeId:scheduleInfos.bridgeId,bodyType:value})
        }
    }
    const handleOk = () => {
        form.validateFields((err, fieldsValue) => {
            if (err) return;
            const params = {
                ...fieldsValue,
                'planBeginDate': fieldsValue['planBeginDate'].format('YYYY-MM-DD'),
                'planEndDate': fieldsValue['planEndDate'].format('YYYY-MM-DD'),
            };
            //编辑状态下 需要传id
            isEdit && (params['id'] = current.id);
            api.createBody(params).then(res => {
                if (res.code == 1) {
                    message.success(isEdit ? '编辑成功' : '添加成功');
                    handleCancel()
                } else {
                    message.warning(res.msg);
                }
            })
        });
    };

    const disabledStartDate = startValue => {
        const endValue = getFieldValue('planEndDate');
        if (!startValue || !endValue) {
            return false;
        }
        // console.log(endValue.valueOf());
        return startValue.valueOf() > endValue.valueOf() + 86400000;
    };

    const disabledEndDate = endValue => {
        const startValue = getFieldValue('planBeginDate');
        if (!endValue || !startValue) {
            return false;
        }
        return endValue.valueOf() < startValue.valueOf();
    };


    const compareName = (rule, value, callback) => {
        const bridgeId = getFieldValue('bridgeId');
        console.log(current.id);
        const params = current && current.id ? {
            "id": current.id,
            "bridgeId": bridgeId,
            "name": value

        } : {
            "bridgeId": bridgeId,
            "name": value
        }

        api.getNameUnique(params).then(res => {
            if (value && !res.data.result) {
                callback('相同名称已存在!');
            } else {
                callback();
            }
        })
    }

    return (
        <Modal
            maskClosable={false}
            width={750}
            title={title}
            visible={visible}
            onCancel={handleCancel}
            destroyOnClose
            footer={[
                <Button key='ok' type="primary" onClick={handleOk}>
                    确定
                </Button>,
                <Button key='cancle' onClick={handleCancel}>
                    取消
                </Button>,
            ]}
        >
            <Form onSubmit={handleOk} layout="inline" hideRequiredMark={false} autocomplete="off" className='schedule-setting'>
                <Row >
                    <Col span={12} style={{
                        height: 60
                    }}>
                        <FormItem label="桥梁名称：">
                            {getFieldDecorator('bridgeId', {
                                initialValue: isEdit ? scheduleInfos.bridgeId!=''?scheduleInfos.bridgeId:(currentBridge ? currentBridge.k : '') : (scheduleInfos.bridgeId!=''?scheduleInfos.bridgeId:bridges&&bridges[0].k)
                            })(
                                <Select
                                    placeholder="请选择"
                                    onChange ={(value,option)=>handleChange(value,option,'name')}
                                    style={{
                                        width: 250
                                    }}>
                                    {
                                        bridges&&bridges.map(item => (<Option value={item.k} key={item.v}>{item.v}</Option>))
                                    }
                                </Select>
                            )}
                        </FormItem>
                    </Col>
                    <Col span={12} style={{
                        height: 60
                    }}>
                        <FormItem label="桩号范围：">
                            {getFieldDecorator('mileage', {
                                rules: [
                                    {
                                        required: true,
                                        message: '请输入桩号范围'
                                    }
                                ],
                                initialValue: isEdit ? current.mileage : '',
                            })(<Input placeholder="请输入桩号范围"
                                      maxLength={20}
                                      style={{
                                          width: 250
                                      }}/>)}
                        </FormItem>
                    </Col>
                </Row>
                <Row gutter={30}>
                    <Col span={12} style={{
                        height: 60
                    }}>
                        <FormItem label="梁体类型：">
                            {getFieldDecorator('typeId', {
                                initialValue: isEdit ? scheduleInfos.bodyType!=''?scheduleInfos.bodyType:(currentBodyType ? currentBodyType.k : '') : scheduleInfos.bodyType!=''?scheduleInfos.bodyType:bodyTypes&&bodyTypes[0].k,
                            })(
                                <Select
                                    placeholder="请选择"
                                    onChange ={(value,option)=>handleChange(value,option,'type')}
                                    style={{
                                        width: 250
                                    }}>
                                    {
                                        bodyTypes&&bodyTypes.map(item => (<Option value={item.k} key={item.v}>{item.v}</Option>))
                                    }
                                </Select>
                            )}
                        </FormItem>
                    </Col>
                    <Col span={12} style={{
                        height: 60
                    }}>
                        <FormItem label="梁体名称：">
                            {getFieldDecorator('name', {
                                rules: [
                                    {
                                        required: true,
                                        message: '请输入梁体名称'
                                    }, {
                                        validator: compareName,
                                    }
                                ],

                                initialValue: isEdit ? current.name : '',
                            })(<Input placeholder="请输入"
                                      maxLength={20}
                                      type ="text"
                                      style={{
                                          width: 250
                                      }}/>)}
                        </FormItem>
                    </Col>
                </Row>
                <Row gutter={30}>
                    <Col span={12} style={{
                        height: 60
                    }}>
                        <FormItem label="计划开始：">
                            {getFieldDecorator('planBeginDate', {
                                    rules: [
                                        {
                                            required: true,
                                            message: '请选择开始日期'
                                        }
                                    ],
                                    initialValue: isEdit ? moment(current.planBeginDate) : null,
                                }
                            )(<DatePicker
                                disabledDate={disabledStartDate}
                                format="YYYY-MM-DD"
                                style={{
                                    width: 250
                                }}
                                placeholder="请输入开始日期"/>)}
                        </FormItem>
                    </Col>
                    <Col span={12} style={{
                        height: 60
                    }}>
                        <FormItem label="计划结束：">
                            {getFieldDecorator('planEndDate', {
                                rules: [
                                    {
                                        required: true,
                                        message: '请选择结束日期'
                                    }
                                    // , {
                                    //     validator: compareDate,
                                    // }
                                ],
                                initialValue: isEdit ? moment(current.planEndDate) : null,
                            })(<DatePicker
                                disabledDate={disabledEndDate}
                                format="YYYY-MM-DD"
                                style={{
                                    width: 250
                                }}
                                placeholder="请输入结束日期"/>)}
                        </FormItem>
                    </Col>
                </Row>
            </Form>
        </Modal>
    );
})

class ScheduleSetting extends PureComponent {
    state = {
        page: 1,
        size: 20,
        totalCount: 0,
        current: {},
        visible: false,
        title: '',
        bodyList: [],
        selectedRowKeys: [],
        beamInfo:[],
        loading: false,
        codeVisible: false,
        columns : [
            {
                title: '序号',
                key: 'index',
                width: 100,
                render: (text, record, index) => index + 1
            }, {
                title: '桥梁名称',
                width: 200,
                dataIndex: 'bridgeName',
                render:(text) => {
                    return <LineWrap title={text}/>
                }
            }, {
                title: '桩号范围',
                width: 200,
                dataIndex: 'mileage',
                render:(text) => {
                    return <LineWrap title={text} />
                }
            }, {
                title: '梁体类型',
                width: 120,
                ellipsis: true,
                dataIndex: 'typeName',
                render:(text) => {
                    return <LineWrap title={text} />
                }
            }, {
                title: '梁体名称',
                width: 150,
                dataIndex: 'name',
                render:(text) => {
                    return <LineWrap title={text} />
                }
            }, {
                title: '计划开始',
                width: 100,
                dataIndex: 'planBeginDate',
                render: (text) =>
                    <LineWrap title={moment(text).format('YYYY-MM-DD')}/>
            }, {
                title: '计划结束',
                width: 100,
                dataIndex: 'planEndDate',
                render: (text) =>
                    <LineWrap title={moment(text).format('YYYY-MM-DD')}/>
            }, {
                title: '关联BIM',
                width: 120,
                dataIndex: 'handle',
                // filterDropdown: (<div></div>),
                /* filterIcon: <div>
                    <i className="bim-reload" onClick={ this.bimModelrefresh} ></i>
                </div>, */
                render: (text) =>
                    <LineWrap title={text}/>
            }, {
                title: '二维码',
                dataIndex: 'code',
                width: 100,
                align: 'center',
                /* render: (text, record) =>{
                    const authCode = this.props.authCode.toJS();
                    return (<AuthButton source="fun" authcode="77005008" authcodes={authCode.funAuthCode} className="button-icon" onClick={() => this.handleCode(record)}><i className="qrcode" /></AuthButton>)
                } */
                render:(text,record)=><i className="qrcode" onClick={() => this.handleCode(record)}/>
            }, {
                title: '操作',
                dataIndex: 'operation',
                align: 'center',
                width: 100,
                render: (text, record) => {
                    const authCode = this.props.authCode.toJS();
                    return (<AuthButton source="fun" authcode="77005002" authcodes={authCode.funAuthCode} className="button-icon" onClick={() => this.handleEdit(record)}><i className="edit" title="编辑" /></AuthButton>)
                }
            }
        ]
    };
    components = {
        header: {
            cell: ResizeableTitle,
        },
    };
    handleCancel = () => {
        this.setState({visible: false, codeVisible: false});
        this.getBody();
    };

    handleEdit = async item => {
        const {data: { data }} = await api.getUserInfo(localStorage.getItem('beamField'));
        this.setState({
            beamInfo:data,
            visible: true,
            current: item,
            title: '编辑梁体',
        });
    };
    // 二維碼
    handleCode = item => {
        if (item.id) {
            this.setState({
                codeVisible: true,
                current: {...item,type:"body"}
            });
        }
    };
    // 新建梁体
    handleCreate = async () => {
        const {data: { data }} = await api.getUserInfo(localStorage.getItem('beamField'));
        this.setState({
            beamInfo:data,
            visible: true,
            current: {},
            title: '新增梁体'
        });
    };
    handleRowSelectChange = (selectedRowKeys, selectedRows) => {
        this.setState({
            selectedRowKeys: selectedRows.map(row => row.id)
        })
    };
    onChange = (current, size) => {
        this.setState({
            page: current
        }, () => {
            this.getBody()
        })
    };
    onShowSizeChange = (page, pageSize) => {
        this.setState({
            size: pageSize,
            page:page
        }, () => {
            this.getBody()
        })
    };
    //获取梁体列表
    getBody = () => {
        this.setState({loading: true});
        const params = {
            "page": this.state.page,
            "size": this.state.size,
        }
        api.getBody(params).then(res => {
            res.data.items.forEach((val,key)=>{
                val.handle =val.handle===''?'未关联':'已关联'
            })
            this.setState({
                bodyList: res.data.items,
                totalCount: res.data.totalCount,
                loading: false
            })
        })
    };
    //删除梁体列表
    deleteBody = () => {
        const {selectedRowKeys} = this.state;
        let _this = this;
        if (selectedRowKeys && selectedRowKeys.length) {
            if (selectedRowKeys && selectedRowKeys.length) {
                confirm({
                    title: '是否确认删除选中行?',
                    // content: '是否确认删除选中行',
                    okText: '取消',
                    cancelText: '确认',
                    onOk() {
                        console.log('OK');
                    },
                    onCancel() {
                        api.deleteBody(selectedRowKeys).then(res => {
                            if (res.code == 1) {
                                message.success('删除成功');
                                _this.getBody();
                            }
                        })
                    },
                });
            }
        }
    }

    // 批量导出二维码
    batchExport = () => {
        const {selectedRowKeys} = this.state;
        if (selectedRowKeys && selectedRowKeys.length) {
            let params = ''
            selectedRowKeys.forEach(row =>
                params += 'ids=' + row + '&'
            )
            params = params.substring(0, params.length - 1)
            const aLink = document.createElement('a');
            document.body.appendChild(aLink);
            aLink.style.display = 'none';
            // aLink.download = `梁场二维码.zip`;
            aLink.href = `${BASE_URL}api/ibf/body/qrCode?${params}`;
            aLink.click();
            document.body.removeChild(aLink);
        }
    }
    //bim刷新
    bimModelrefresh = ()=>{
        // console.log('bim刷新')
    }
    handleResize = index => (e, { size }) => {
        this.setState(({ columns }) => {
          const nextColumns = [...columns];
          nextColumns[index] = {
            ...nextColumns[index],
            width: size.width,
          };
          return { columns: nextColumns };
        });
    };
    render() {
        const rowSelection = {
            onChange: this.handleRowSelectChange
        };
        const columns = this.state.columns.map((col, index) => ({
            ...col,
            onHeaderCell: column => ({
              width: column.width,
              onResize: this.handleResize(index),
            }),
        }));
        const beamInfo = this.props.beamInfo.toJS();
        const authCode = this.props.authCode.toJS();
        const {bridges, bodyTypes} = this.state.beamInfo;
        const {bodyList, visible, title, current, page, size, totalCount, loading, codeVisible} = this.state
        const parentMethods = {
            handleOk: this.handleOk,
            handleCancel: this.handleCancel,
            handleSearch: this.handleSearch
        };
        return (
            <Layout>
                <div className='layout-header'>
                    <AuthButton type="primary" onClick={this.handleCreate}
                                source = "fun" authcode='77005001' authcodes = {authCode.funAuthCode}>新增</AuthButton>
                    <AuthButton type="primary" onClick={this.deleteBody} source = "fun" 
                                authcode='77005003' authcodes = {authCode.funAuthCode}>删除</AuthButton>
                    <Button type="primary" onClick={this.batchExport}>导出二维码</Button>
                </div>
                <Table
                    rowKey="id"
                    scroll={{y: 600}}
                    pagination={{
                        current: page,
                        pageSize: size,
                        showSizeChanger: true,
                        showQuickJumper: true,
                        pageSizeOptions: ['20', '30', '50'],
                        onChange: this.onChange,
                        total: totalCount,
                        showTotal: total => (`共${total}条`),
                        onShowSizeChange: this.onShowSizeChange
                    }}
                    loading={loading}
                    rowSelection={rowSelection}
                    components={this.components}
                    columns={columns}
                    dataSource={bodyList}
                    bordered={true}/>
                <ModalForm {...parentMethods} bridges={bridges} bodyTypes={bodyTypes} visible={visible} title={title}
                           current={current} setValue = {this.props.setValue}
                           scheduleInfos = {this.props.scheduleInfos}></ModalForm>
                <CodeModal handleCancel={this.handleCancel} visible={codeVisible} current={current}></CodeModal>
            </Layout>
        )
    };

    componentDidMount() {
        console.log('this.props.beamInfo', this.props.beamInfo.toJS());
        this.getBody()
    }
}

const mapStateToProps = state => {
    return {
        beamInfo: state.getIn(["login", "beamInfo"]),
        scheduleInfos: state.getIn(["panel", "scheduleInfos"]),
        authCode: state.getIn(["login", "authCode"]),
    };
};
const mapDispatchToProps = {
    setValue:(itemInfo) => actionCreators.scheduleSettingAddInfo(itemInfo)
}
export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(ScheduleSetting);